.CCroot {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}
.CCmaindiv {
    width: 1140px;
    height: 683px;
    border-radius: 14px;
    display: flex;
    background-color: #000;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.CBackBtnOut {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
    top: 34px;
    left: 42px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #86a9f3;
    .CBackBtn {
        font-size: 25px;
        font-weight: bold;
        color: #000 !important;
    }
}
.CNextBtnOut {
    width: 50px;
    height: 50px;
    cursor: pointer;
    right: 33px;
    bottom: 44px;
    border-radius: 50%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 3px solid #86a9f3;
    .CNextBtn {
        color: #86a9f3;
        font-size: 25px;
    }
}
.CCtitle {
    color: #b9d7ff;
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 44px;
    line-height: 53px;
    margin-bottom: 9px;
}
.CProfessionalDiv {
    width: 483px;
    height: 154px;
    background: #777777;
    border-radius: 30px;
    margin-top: 46px;
    display: flex;
    align-items: center;
    padding: 19px 21px 24px 44px;
    .Ccheck-radio {
        appearance: none;
        width: 55px;
        min-width: 55px;
        height: 55px;
        border: 7px solid #fff;
        border-radius: 50%;
        margin-right: 40px;
        position: relative;
        display: flex;
        background-color: #b0b0b0;
        justify-content: center;
        align-items: center;
    }
    .Ccheck-radio:checked {
        background-color: #0057ff;
    }
}
.CradioText {
    color: #fff;
    font-family: Inter;
    font-style: normal;
    font-size: 20px;
    p {
        font-weight: 600;
        font-size: 24px;
        line-height: 35px;
        margin: 0;
        color: white;
        margin-bottom: 5px;
    }
}
